body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

html {
  font-size: 16px;
  line-height: 1.2;
  font-weight: 500;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  font-family: "Geometria", sans-serif;
  background-color: #30c2c6;
  color: #ffffff;
  /* iOS fix */
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  -webkit-locale: auto;
  white-space: normal;
}

main {
  position: relative;
  z-index: 1;
}

section {
  transition: opacity 1s ease;
}

a {
  color: #ffffff;
  text-decoration: none;
}

svg {
  width: auto;
  height: auto;
}

img {
  display: block;
  max-width: 100%;
}

input,
button {
  -webkit-appearance: none;
}

input,
button,
select,
textarea {
  font: inherit;
  background-color: transparent;
  border: 0;
  border-radius: 0;
  outline: 0;
}

input,
textarea {
  padding: 0;
  margin: 0;
}
input:focus::placeholder,
textarea:focus::placeholder {
  opacity: 0;
}

ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

[hidden] {
  display: none !important;
}

/* Blocks */
.container {
  max-width: 1080px;
  width: 100%;
  padding: 0 40px;
  margin: 0 auto;
  box-sizing: border-box;
}
.container--small {
  max-width: 1000px;
}

.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  max-width: 243px;
  width: 100%;
  padding: 14px;
  border-radius: 3px;
  background-color: #ffffff;
  color: #30c2c6;
  font-size: 16px;
  line-height: 1.2;
  text-align: center;
  cursor: pointer;
  transition: 0.3s ease;
}
.button:hover {
  background-color: rgba(255, 255, 255, 0.7);
}
.button--green {
  background-color: #30c2c6;
  color: #ffffff;
}
.button--green:hover {
  background-color: rgba(48, 194, 198, 0.7);
}

.title {
  font-size: 30px;
  text-transform: uppercase;
}
.title span {
  display: block;
  font-size: 24px;
  text-transform: lowercase;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  height: 145px;
  padding: 45px 0;
  pointer-events: none;
}
.header__container {
  display: flex;
  align-items: center;
  gap: 46px;
}
.header__logo {
  pointer-events: auto;
}

.hero {
  position: relative;
}
.hero__bg {
  position: absolute;
  top: 0;
  right: 0;
  width: 52%;
}
.hero__bg img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  aspect-ratio: 19/5;
}
.hero__logo {
  position: absolute;
  bottom: 29px;
  left: 51px;
}
.hero__container {
  position: relative;
  display: flex;
  align-items: flex-end;
  justify-content: flex-start;
  gap: 83px;
  padding-top: 149px;
  padding-bottom: 40px;
  border: 1px solid #ffffff;
  border-top: 0;
  pointer-events: none;
}
.hero__dots {
  position: absolute;
  bottom: -4px;
  left: calc(100% + 33px);
  color: rgba(255, 255, 255, 0.44);
}
.hero__content {
  max-width: 360px;
  width: 100%;
  pointer-events: auto;
}
.hero__title {
  font-weight: 700;
  font-size: 57px;
  line-height: 1.1;
  text-transform: uppercase;
}
.hero__text {
  font-size: 23px;
  margin-top: 22px;
  padding-left: 10px;
  text-indent: -10px;
}
.hero__slider {
  max-width: 406px;
  pointer-events: auto;
}
.hero__slide {
  font-size: 14px;
  line-height: 1.3;
}

.scheme {
  padding: 24px 0 70px;
}
.scheme__container {
  max-width: 1190px;
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
}
.scheme__item {
  padding: 16px 10px 8px;
  background-color: #ffffff;
  overflow: hidden;
}
.scheme__item:first-child {
  width: calc(69.6% - 8px);
}
.scheme__item:last-child {
  width: calc(30.4% - 8px);
}
.scheme__item--small .scheme__title, .scheme__item--small .scheme__text {
  padding: 0 18px;
}
.scheme__item--small .scheme__image {
  width: calc(100% + 60px);
  margin-top: 35px;
  margin-left: -30px;
}
.scheme__item--small .scheme__image img {
  margin: 0 auto;
}
.scheme__title, .scheme__text {
  color: #30c2c6;
  font-weight: 700;
  padding: 0 43px;
}
.scheme__title {
  font-size: 26px;
  text-transform: uppercase;
}
.scheme__text {
  font-size: 18px;
  margin-top: 10px;
}
.scheme__image {
  overflow: hidden;
  margin-top: 20px;
}
.scheme__image img {
  margin: 0 30px 0 auto;
}

.articles-card {
  display: flex;
  gap: 36px;
}
.articles-card__photo {
  max-width: 148px;
  width: 100%;
  height: 148px;
  border-radius: 50%;
  overflow: hidden;
}
.articles-card__photo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.articles-card__content {
  flex-grow: 1;
}
.articles-card__name, .articles-card__quote {
  font-size: 22px;
}
.articles-card__name {
  font-weight: 700;
}
.articles-card__bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  margin-top: 30px;
}
.articles-card__text {
  max-width: 370px;
  font-size: 16px;
  line-height: 1.3;
}
.articles-card__link {
  flex-grow: 1;
}

.articles {
  position: relative;
  padding: 0 0 70px;
}
.articles__dots {
  position: absolute;
  top: 263px;
  left: -34px;
  color: rgba(92, 132, 143, 0.44);
}
.articles__container {
  border: 1px solid #ffffff;
  padding-bottom: 70px;
}
.articles__title {
  display: inline-block;
  background-color: #30c2c6;
  padding: 0 23px;
  margin-top: -25px;
}
.articles__wrapper {
  display: flex;
  flex-direction: column;
  gap: 47px;
  padding: 0 25px;
  margin-top: 51px;
}

.products-card {
  display: flex;
  gap: 24px;
  background-color: #ffffff;
  border-radius: 3px;
  padding: 48px 60px 33px;
}
.products-card__content {
  max-width: 437px;
}
.products-card__title {
  color: #30c2c6;
  font-weight: 700;
  font-size: 24px;
}
.products-card__list {
  margin-top: 30px;
}
.products-card__list li {
  display: flex;
  align-items: flex-start;
  gap: 8px;
  color: #000000;
  font-size: 16px;
}
.products-card__list li + li {
  margin-top: 23px;
}
.products-card__list li::before {
  position: relative;
  top: 3px;
  content: "";
  min-width: 13px;
  max-width: 13px;
  height: 13px;
  background-color: #30c2c6;
}
.products-card__image img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.products-card__link {
  max-width: 210px;
  margin-top: 31px;
}

.products {
  position: relative;
  padding-bottom: 33px;
}
.products::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 28px;
  right: 28px;
  border: 3px solid #30c2c6;
  pointer-events: none;
}
.products__title {
  position: relative;
  z-index: 1;
  top: -16px;
  left: 50%;
  transform: translateX(-50%);
  display: inline-block;
  background-color: #5c848f;
  padding: 0 20px;
  font-size: 28px;
  text-align: center;
}
.products__wrapper {
  display: grid;
  gap: 24px;
  margin-top: 26px;
}

.safety {
  position: relative;
  padding: 50px 0 50px;
  background-color: #5c848f;
  overflow: hidden;
}
.safety__dots-wrapper {
  display: flex;
  gap: 20px;
  position: absolute;
  top: 15px;
  right: -3px;
}
.safety__dots {
  color: rgba(255, 255, 255, 0.44);
}
.safety__title {
  padding-left: 60px;
  text-indent: -60px;
}
.safety__products {
  margin-top: 58px;
}

.article-preview {
  display: flex;
  align-items: center;
  gap: 30px;
  padding-top: 50px;
}
.article-preview__image {
  width: calc(47.3% - 15px);
  border-radius: 3px;
  overflow: hidden;
}
.article-preview__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.article-preview__content {
  display: flex;
  flex-direction: column;
  gap: 24px;
  width: calc(52.7% - 15px);
}
.article-preview__title {
  font-weight: 700;
  font-size: 24px;
}
.article-preview__text {
  font-size: 16px;
}
.article-preview__link {
  max-width: 221px;
}

.chart {
  padding-top: 41px;
}
.chart__window {
  background-color: #ffffff;
  padding: 30px 60px 20px;
  border-radius: 3px;
}
.chart__title {
  max-width: 670px;
  margin: 0 auto;
  color: #30c2c6;
  font-weight: 700;
  font-size: 24px;
  text-align: center;
}
.chart__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  margin-top: 13px;
}
.chart__col {
  display: flex;
  flex-direction: column;
}
.chart__legend {
  display: flex;
  align-items: center;
  gap: 15px;
}
.chart__legend + .chart__legend {
  margin-top: 45px;
}
.chart__legend--reverse {
  flex-direction: row-reverse;
}
.chart__legend--reverse .chart__label {
  text-align: right;
}
.chart__square {
  min-width: 27px;
  height: 27px;
}
.chart__number {
  font-weight: 700;
  font-size: 24px;
}
.chart__label {
  color: #000000;
  font-size: 13px;
}
.chart__canvas {
  position: relative;
  min-width: 340px;
  max-width: 340px;
  height: 340px;
}
.chart__canvas-image {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 131px;
  height: 131px;
  background-color: #f7f7f7;
  border-radius: 50%;
  box-shadow: 0 0 11.6px 4px rgba(0, 0, 0, 0.19);
}
.chart__source {
  color: #5c848f;
  font-size: 11px;
  margin-top: 30px;
}